import React from 'react';
import { Icon, Avatar } from 'antd';
import { Link, Redirect, Route, Switch } from 'react-router-dom';
import classnames from 'classnames';
import Login from '@panel/User/Login';
import Register from '@panel/User/Register';
import s, { IIndexLess } from './index.less';
const styles = s as Partial<IIndexLess>;
export interface IUserLayoutProps {
  history?: History;
  location?: Location;
  dispatch?: (param: object) => Promise<any>;
  match: {
    path: string;
    url: string;
    params: {
      url: 'login' | 'register';
    };
  };
}
export default function(props: IUserLayoutProps) {
  const { match } = props;
  return (
    <div className={styles.wrap}>
      <div className={styles.top}>
        <div className={styles.header}>
          <Link to="/">
            <Avatar className={styles.logo} size="large">
              孙
            </Avatar>
            <span className={classnames(styles.title,styles.lightText)}>赵妹妹的私人小站</span>
          </Link>
        </div>
        <div className={classnames(styles.desc,styles.lightText)}>By 青</div>
      </div>
      <Switch>
        <Route path="/user/login" component={Login} exact />
        <Route path="/user/register" component={Register} exact />
        <Redirect exact from="/user" to="/user/login" />
      </Switch>
    </div>
  );
}
