import React from 'react';
import { Input, Icon } from 'antd';
import { connect, DispatchProp } from 'react-redux';
import s, { IIndexLess } from './index.less';
const styles = s as Partial<IIndexLess>;
const { Search } = Input;
interface IFoodItem {
  name: string;
}
type IFoodPanelProps = {
  userStore: IUser.Model;
} & IG.IProps &
  DispatchProp & {};
interface IFoodPanelState {
  list: IFoodItem[];
}
class FoodPanel extends React.PureComponent<IFoodPanelProps, IFoodPanelState> {
  state = {
    list: JSON.parse(localStorage.getItem('foods') || '[]'),
  };
  handleAdd = value => {
    const { list } = this.state;
    this.setState({
      list: list.concat([
        {
          name: value,
        },
      ]),
    });
  };
  render() {
    const {list} = this.state;
    return (
      <div className={styles.wrap}>
        <div className={styles.head}>
          <Search
            placeholder="请输入链接"
            size="large"
            enterButton={<Icon type="plus" />}
            onSearch={this.handleAdd}
          />
        </div>
        <div className={styles.body}></div>
      </div>
    );
  }
}
export default connect(({ user, loading }: any) => ({
  userStore: user,
}))(FoodPanel);
