import React from 'react';
import { connect, DispatchProp } from 'react-redux';
import {Switch,Redirect,Route} from 'react-router-dom';
import { Layout } from 'antd';
import { ContainerQuery } from 'react-container-query';
import classNames from 'classnames';
import { enquireScreen } from 'enquire-js';
import GlobalHeader from '@components/GlobalHeader';
import SiderMenu from '@components/SiderMenu';
import logo from '@assets/logo.svg';
import MainPanel from '@panel/Main';
import FoodPanel from '@panel/Food';

import { getMenuData } from './menu';
import s, { IIndexLess } from './index.less';
const { Content, Header } = Layout;
const query = {
  'screen-xs': {
    maxWidth: 575,
  },
  'screen-sm': {
    minWidth: 576,
    maxWidth: 767,
  },
  'screen-md': {
    minWidth: 768,
    maxWidth: 991,
  },
  'screen-lg': {
    minWidth: 992,
    maxWidth: 1199,
  },
  'screen-xl': {
    minWidth: 1200,
  },
};

let isMobile;
enquireScreen(b => {
  isMobile = b;
});
/**
 * 根据菜单取得重定向地址.
 */
const redirectData = [];
const getRedirect = item => {
  if (item && item.children) {
    if (item.children[0] && item.children[0].path) {
      redirectData.push({
        from: `${item.path}`,
        to: `${item.children[0].path}`,
      });
      item.children.forEach(children => {
        getRedirect(children);
      });
    }
  }
};
getMenuData().forEach(getRedirect);
const styles = s as Partial<IIndexLess>;
type IBaseLayoutProps = {
  userStore: IUser.Model;
} & IG.IProps &
  DispatchProp & {};
interface IBaseLayoutState {
  isMobile: boolean;
}
class BaseLayout extends React.PureComponent<
  IBaseLayoutProps,
  IBaseLayoutState
> {
  state = {
    isMobile,
  };
  componentDidMount() {
    const { userStore, dispatch } = this.props;
    enquireScreen(mobile => {
      this.setState({
        isMobile: mobile,
      });
    });
    if (!userStore.loginInfo.status) {
      dispatch({
        type: 'saga/user/loginLast',
      });
    }
  }
  private handleMenuCollapse = collapsed => {
    this.props.dispatch({
      type: 'saga/user/changeCollapsed',
      payload: collapsed,
    });
  };
  private handleMenuClick = ({ key }) => {
    
  };
  render() {
    const {  userStore, location,match } = this.props;
    console.log(match);
    const {isMobile} = this.state;
    const {collapsed,info} = userStore;
    const layout = info?(
      <Layout style={{ width: '100%', height: '100%' }}>
        <SiderMenu
          logo={logo}
          menuData={getMenuData()}
          collapsed={collapsed}
          location={location}
          isMobile={isMobile}
          onCollapse={this.handleMenuCollapse}
        />
        <Layout>
          <Header style={{ padding: 0 }}>
            <GlobalHeader
              logo={logo}
              currentUser={info}
              collapsed={collapsed}
              isMobile={isMobile}
              onCollapse={this.handleMenuCollapse}
              onMenuClick={this.handleMenuClick}
            />
          </Header>
          <Content style={{ height: '100%' }}>
            <Switch>
              {redirectData.map(item => (
                <Redirect key={item.from} exact from={item.from} to={item.to} />
              ))}
              <Route path="/main" component={MainPanel} exact />
              <Route path="/food" component={FoodPanel} exact />
              
              <Redirect from="/" to='/main' exact />
              
            </Switch>
          </Content>
        </Layout>
      </Layout>
    ):null;

    return (
      <ContainerQuery query={query}>
        {params => <div className={classNames(params)}>{layout}</div>}
      </ContainerQuery>
    );
  }
}
export default connect(({ user }: any) => ({ userStore: user }))(BaseLayout);
