import request from '@utils/request';

export async function login({tel,pwd,token}:IUser.LoginParam):Promise<IUser.LoginRs> {
  let body;
  if (token) {
    body = {
      token,
    };
  } else {
    body = {
      tel,
      pwd,
    };
  }
  return request(`/api/user/login`,{
    method: 'POST',
    body,
  });
}

export async function logout(code) {
    return request(`/api/${code}`);
  }